@use "global" as *;

/*wp-pagenavi 01*/
.wp-pagenavi {
  clear: both;
  text-align: center;
  display: flex;
  justify-content: center;
}
.wp-pagenavi a,
.wp-pagenavi span {
  color: $black;
  font-family: $third-font-family;
  background-color: transparent;
  border: rem(1) solid $black;
  padding: rem(6) rem(12);
  margin: 0 rem(6);
  white-space: nowrap;
  transition: 0.2s ease-in-out;
  text-align: center;
  text-decoration: none;
}
.wp-pagenavi a:hover {
  color: $base;
  background-color: $black;
  border-color: $black;
}
.wp-pagenavi span.current {
  color: $base;
  background-color: $black;
  border-color: $black;
  // font-weight: bold;
}

.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  // padding: rem(3) rem(15);
  margin: 0 rem(40);
}

.wp-pagenavi .larger:nth-child(n+5) {
  display: none;
  @include mq(md){
  display: block;
  }
}
