.button {
  background: #408F95;
  color: #fff;
  display: inline-block;
  position: relative;
  font-weight: 300;
  width: 100%;
  padding: 1rem 2.375rem 1rem 2.375rem;
  cursor: pointer;
  text-align: left;
  max-width: 12.625rem;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  z-index: 1;
  border: 0.0625rem solid #408F95;
}

.button.button--reverse {
  background: #fff;
  color: #408F95;
  border: 0.0625rem solid #fff;
}

.button.button--reverse:hover {
  transform: translate(0.125rem, 0.125rem);
  border: 0.0625rem solid #fff;
}

.button.button--reverse::before {
  border: 0.0625rem solid #fff;
}

.button.button--reverse:hover::before {
  border: none;
}

.button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover {
  color: #408F95;
  border: 0.0625rem solid #408F95;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button span {
  position: relative;
}

.button span::after {
  position: absolute;
  content: url(../images/common/button-w.svg);
  top: -0.375rem;
  left: 0.5rem;
}

.button.button--reverse span::after {
  content: url(../images/common/button-g.svg);
}

.button:hover span::after {
  content: url(../images/common/button-g.svg);
}

.button::before {
  border: 0.0625rem solid #408F95;
  bottom: -0.25rem;
  content: "";
  left: 0.25rem;
  position: absolute;
  right: -0.25rem;
  top: 0.25rem;
  z-index: -1;
}

